import axios from "axios";
import serverAddress from '../util/serverAddress';

const base_address = serverAddress();

const listdbs = async() => {

    const response = await axios({
        method: 'get',
        url: `${base_address}/api/queries/dbs`, 
        headers: {
            'Content-Type': 'application/json',
            'x-access-token': localStorage.getItem("query-auth-token")

        }
    })
    return response;

}

const listcollections = async(dbname) => {

    const response = await axios({
        method: 'post',
        url: `${base_address}/api/queries/dbcollections`, 
        headers: {
            'Content-Type': 'application/json',
            'x-access-token': localStorage.getItem("query-auth-token")


        },
        data: {
            dbname: dbname
        }
    })
    return response;

}

const listKeys = async(dbname, collectionname) => {

    const response = await axios({
        method: 'post',
        url: `${base_address}/api/queries/collectionfields`, 
        headers: {
            'Content-Type': 'application/json',
            'x-access-token': localStorage.getItem("query-auth-token")

        },
        data: {
            dbname,
            collectionname
        }
    })
    return response;

}

const queryData = async(dbname, collectionname, listoffields) => {

    const response = await axios({
        method: 'post',
        url: `${base_address}/api/queries/query`, 
        headers: {
            'Content-Type': 'application/json',
            'x-access-token': localStorage.getItem("query-auth-token")

        },
        data: {
            dbname,
            collectionname,
            listoffields
        }
    })
    return response;

}

export { listdbs, listcollections, listKeys, queryData };