import React, { useEffect, useState } from "react"
import {
    listdbs,
    listcollections,
    listKeys,
    queryData,
} from "../../services/dbServices"
import { Grid, Checkbox } from "@material-ui/core"
import InputLabel from "@material-ui/core/InputLabel"
import FormHelperText from "@material-ui/core/FormHelperText"
import FormControl from "@material-ui/core/FormControl"
import NativeSelect from "@material-ui/core/NativeSelect"
import { makeStyles } from "@material-ui/core/styles"
import FormGroup from "@material-ui/core/FormGroup"
import FormControlLabel from "@material-ui/core/FormControlLabel"
import QueryTable from "./QueryTable"

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}))

export default function Query(props) {
    const classes = useStyles()
    const [dbs, setdbs] = useState([])
    const [collections, setCollections] = useState([])
    const [keys, setKeys] = useState([])
    const [columns, setColumns] = useState([])
    const [data, setData] = useState([])
    const [desiredKeys, setDesiredKeys] = useState([])

    const [selectedDb, setSelectedDb] = useState("")
    const [selectedCollection, setSelectedCollection] = useState("")

    useEffect(() => {
        ;(async function getFbs() {
            let dbs = await listdbs()
            setdbs(dbs.data)
        })()
    }, [])

    useEffect(() => {
        ;(async function getCollections() {
            let collections = await listcollections(selectedDb)
            setCollections(collections.data)
        })()
    }, [selectedDb])

    useEffect(() => {
        ;(async function getKeys() {
            setKeys([])
            let fetchedKeys = await listKeys(selectedDb, selectedCollection)
            setKeys(fetchedKeys.data)
            setDesiredKeys(fetchedKeys.data)
        })()
    }, [selectedDb, selectedCollection])

    useEffect(() => {
        if (desiredKeys) {
            let columns = desiredKeys.map((column) => {
                return { Header: column, accessor: column }
            })
            setColumns(columns)
        }
    }, [desiredKeys])

    useEffect(() => {
        ;(async function getData() {
            setData([])
            let rawData = await queryData(
                selectedDb,
                selectedCollection,
                desiredKeys
            )
            if (rawData.data) {
                let processedData = rawData.data.map((detail) => {
                    let line = {}
                    desiredKeys.forEach((key) => {
                        if (typeof detail[key] !== "object") {
                            line[key] = detail[key]
                        } else {
                            line[key] = JSON.stringify(detail[key])
                        }
                    })
                    return line
                })
                setData(processedData)
            }
        })()
    }, [selectedDb, selectedCollection, desiredKeys])

    const fetchCollections = async (e) => {
        setCollections([])
        setSelectedDb(e.target.value)
    }

    const fetchTableContent = async (e) => {
        let collectionName = e.target.value
        setSelectedCollection(collectionName)
    }

    const selectCheckBox = (e) => {
        if (desiredKeys.includes(e.target.name)) {
            let reducedKeys = desiredKeys.filter((key) => key !== e.target.name)
            setDesiredKeys(reducedKeys)
        } else {
            setDesiredKeys(desiredKeys.concat(e.target.name))
        }
    }

    const selectAll = () => {
        if (desiredKeys.length === keys.length) {
            setDesiredKeys([])
        } else {
            setDesiredKeys(keys)
        }
    }

    return (
        <div style={{ margin: 50 }}>
            <h1>Dynamic Querying</h1>
            <Grid
                container
                direction="row"
                justify="space-around"
                alignItems="flex-start"
            >
                <Grid item>
                    <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="age-native-helper">DB</InputLabel>
                        <NativeSelect
                            value={selectedDb}
                            onChange={fetchCollections}
                            inputProps={{
                                name: "age",
                                id: "age-native-helper",
                            }}
                        >
                            <option aria-label="None" value="" />
                            {dbs.length > 0 &&
                                dbs.map((dbname, index) => (
                                    <option key={index} value={dbname}>
                                        {dbname}
                                    </option>
                                ))}
                        </NativeSelect>
                        <FormHelperText>Pick DB</FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item>
                    <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="age-native-helper">
                            Collection
                        </InputLabel>
                        <NativeSelect
                            value={selectedCollection}
                            onChange={fetchTableContent}
                            inputProps={{
                                name: "age",
                                id: "age-native-helper",
                            }}
                        >
                            <option aria-label="None" value="" />
                            {collections.length > 0 &&
                                collections.map((collection, index) => (
                                    <option key={index} value={collection}>
                                        {collection}
                                    </option>
                                ))}
                        </NativeSelect>
                        <FormHelperText>Pick Collection</FormHelperText>
                    </FormControl>
                </Grid>
            </Grid>
            <Grid item>
                {keys.length > 0 && (
                    <FormGroup row>
                        <FormControlLabel
                            control={
                                <Checkbox
                                    checked={desiredKeys.length === keys.length}
                                    onChange={selectAll}
                                    name="All"
                                    color="primary"
                                />
                            }
                            label="All"
                        ></FormControlLabel>
                        {keys.map((key, index) => {
                            return (
                                <FormControlLabel
                                    key={index}
                                    control={
                                        <Checkbox
                                            checked={
                                                !!desiredKeys.includes(key)
                                            }
                                            onChange={selectCheckBox}
                                            name={key}
                                            color="primary"
                                        />
                                    }
                                    label={key}
                                />
                            )
                        })}
                    </FormGroup>
                )}
            </Grid>
            {columns.length > 0 && data.length > 0 && (
                <QueryTable columns={columns} data={data} />
            )}
        </div>
    )
}
